<template>
  <div class="row" v-if="isReady">
    <div class="col-12 mt-3 flex-x-between">
      <div class="flex-align-center">
        <h3 class="my-0">Imports</h3>
      </div>

      <div class="flex-align-center flex-end">
        <el-button
          class="mx-0 my-0"
          @click="$router.push('/add-new-import')"
          type="primary"
          v-ripple.400
        >
          Add order
        </el-button>
      </div>
    </div>
    <div class="col-12 list-table mt-3">
      <div class="box pt-3">
        <div class="row">
          <div class="col-12 flex-align-center">
            <p
              class="mx-0 table-tab"
              :class="filters.status === '' && 'active-table-tab'"
              @click="filters.status = ''"
            >
              All
            </p>
          </div>
          <div class="col-12"><div class="b-divider mb-1 mt-0"></div></div>
          <div
            class="
              col-12
              mb-1
              d-flex
              justify-content-between
              align-items-center
            "
          >
            <div class="flex-align-center">
              <el-select
                v-model="filters.pageSize"
                class="mr-2 page-size-selector mr-2"
              >
                <el-option
                  v-for="page in 10"
                  :key="page"
                  :label="page * 5"
                  :value="page * 5"
                >
                </el-option>
              </el-select>
              <el-input
                v-model="filters.query"
                class="mr-2 table-search"
                placeholder="Type something"
                prefix-icon="bx bx-search"
              >
              </el-input>
              <el-select
                v-model="filters.supplier"
                filterable
                clearable
                class="mr-2 filter-input"
                placeholder="Supplier"
              >
                <el-option
                  v-for="supplier in suppliers"
                  :key="supplier.id"
                  :label="supplier.name"
                  :value="supplier.name"
                >
                </el-option>
              </el-select>
              <el-date-picker
                class="mr-2 filter-input"
                v-model="filters.date"
                type="date"
                format="dd/MM/yyyy"
                value-format="dd/MM/yyyy"
                placeholder="Pick a date"
              >
              </el-date-picker>
            </div>

            <el-popover placement="bottom" width="200" trigger="click">
              <p class="font-weight-bold">Sort By</p>
              <div class="b-divider my-2"></div>
              <div class="w-100">
                <el-radio
                  v-model="filters.order_by"
                  v-for="(sort, n) in sortList"
                  :key="sort.name"
                  :class="n !== sortList.length - 1 && 'mb-3'"
                  :label="sort.value"
                >
                  {{ sort.name }}
                </el-radio>
              </div>
              <el-button
                v-ripple.400="'rgba(0,0,0,0.1)'"
                class="flat no-width"
                slot="reference"
              >
                <i class="bx bx-sort mr-1"></i> Sort
              </el-button>
            </el-popover>
          </div>
        </div>
        <div class="w-100 el-main-table" v-loading="loading">
          <div class="el-table-header" v-if="selected.length > 0">
            <div class="d-flex align-items-center">
              <el-checkbox
                :indeterminate="selected.length == orders.results.length"
                v-model="allCheck"
                @change="$refs.multipleTable.toggleAllSelection"
                >{{ selected.length }} selected</el-checkbox
              >
              <el-dropdown @command="updateStatus" trigger="click">
                <p
                  class="el-dropdown-link ml-3 pointer primary hover-underline"
                >
                  Actions
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </p>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item
                    v-for="action in actions"
                    :key="action.value"
                    :command="action.value"
                  >
                    {{ action.label }}</el-dropdown-item
                  >
                  <el-dropdown-item disabled> No Action</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </div>
          </div>
          <el-table
            ref="multipleTable"
            :data="orders.results"
            style="width: 100%"
            @row-click="
              (row) =>
                $router.push({
                  name: 'Edit import',
                  params: { id: row.id },
                })
            "
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection"> </el-table-column>
            <el-table-column width="250" label="Date of order">
              <template slot-scope="scope">
                {{ scope.row.date }}
              </template>
            </el-table-column>
            <el-table-column label="Supplier" width="250">
              <template slot-scope="scope">
                {{ scope.row.supplier_name || "-" }}
              </template>
            </el-table-column>
            <el-table-column label="Total" width="250">
              <template slot-scope="scope">
                {{ scope.row.total + "MMK" }}
              </template>
            </el-table-column>

            <el-table-column label="Items">
              <template slot-scope="scope">
                {{ getItemsQuantity(scope.row) }} Items
              </template>
            </el-table-column>

            <template #empty>
              <el-empty description="No data found!">
                <template #image> <empty /> </template
              ></el-empty>
            </template>
          </el-table>
          <div class="el-table-footer" v-if="orders.total_pages !== 1">
            <el-pagination
              background
              @current-change="
                (index) => ((orders.current_page = index), getOrders())
              "
              :current-page="orders.current_page"
              layout="prev, pager, next"
              :page-count="orders.total_pages"
            >
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { empty } from "../../common";
export default {
  data() {
    return {
      filters: {
        query: "",
        order_by: "-updated_at",
        pageSize: 10,
        status: "",
        supplier: "",
        date: "",
        is_fulfilled: false,
      },
      loading: false,
      actions: [],
      sortList: [
        {
          name: "Created (oldest first)",
          value: "created_at",
        },
        {
          name: "Created (newest first)",
          value: "-created_at",
        },
        {
          name: "Updated (oldest first)",
          value: "updated_at",
        },
        {
          name: "Updated (newest first)",
          value: "-updated_at",
        },
        {
          name: "Supplier A–Z",
          value: "supplier__name",
        },
        {
          name: "Supplier Z–A",
          value: "-supplier__name",
        },
      ],
      suppliers: [],
      selectedDate: null,
      allCheck: false,
      gettingOrder: false,
      page: 1,
      max: 5,
      selected: [],
      orders: {
        current_page: 1,
        results: [],
        total_pages: 0,
      },
    };
  },
  computed: {
    ...mapState(["isReady", "store"]),
  },
  components: {
    empty,
  },
  created() {
    this.$store.commit("setIsReady", false);
    this.getSuppliers();
    this.getOrders();
  },
  watch: {
    "orders.current_page": {
      handler: function () {
        this.getOrders();
      },
      deep: true,
    },
    filters: {
      handler: function () {
        this.getOrders();
      },
      deep: true,
    },
  },
  methods: {
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.selected = val;
    },
    changeActions(action) {
      if (action == "Archive orders") {
        this.updateStatus("Archived");
      } else if (action == "Unarchive orders") {
        this.updateStatus("Active");
      }
    },
    updateStatus(status) {
      this.updatingStatus = true;
      var selectedIds = [];
      this.selected.forEach((sale) => selectedIds.push(sale.id));
      this.$axios
        .put("orders-status/", {
          status: status,
          ids: selectedIds,
        })
        .then(() => {
          this.selected = [];
          this.getOrders();
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    getOrders() {
      this.loading = true;
      this.gettingOrder = true;
      if (this.filters.date === null) {
        this.filters.date = "";
      }
      var url = `orders/?page=${this.orders.current_page}&page_size=${this.filters.pageSize}&query=${this.filters.query}&supplier=${this.filters.supplier}&date=${this.filters.date}&order_by=${this.filters.order_by}`;
      this.$axios
        .get(url)
        .then((response) => {
          this.loading = false;
          this.gettingOrder = false;
          this.orders.results = response.data.results;
          this.orders.total_pages = response.data.total_pages;
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.loading = false;
          this.gettingOrder = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    getSuppliers() {
      var url = `suppliers/?page=1&query=&order_by=-name&page_size=1000000`;
      this.$axios
        .get(url)
        .then((response) => {
          this.suppliers = response.data.results;
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },

    getItemsQuantity(sale) {
      return sale.products.reduce((total, item) => {
        return total + parseFloat(item.quantity);
      }, 0);
    },
  },
};
</script>

